export default class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      problem_sequence : [...problem_sequence],
      problem_number : 0
    }
    window.goToWord = (word) =>{
      this.setState({
        problem_number :
               problem_sequence.indexOf(word.toUpperCase()) || 0
      })
    }
  }

  render = () =>{
  let word = this.state.problem_sequence[this.state.problem_number]
  console.log(word)
  return (
    <View style={styles.container}>
      {<ISProblem
        onDone={()=>{this.setState({problem_number: this.state.problem_number+1})}} 
        word={word}
        grapheme_options = {grapheme_options[word]}
      />}
      {/*<Test style={{height: "100%"}}/>*/}
      
    </View>
  );
  }
}

